<template>
  <div class="address container">
      <van-nav-bar fixed title="通讯录">
      </van-nav-bar>
      <!-- 搜索 -->
      <van-search
        v-model="search"
        placeholder="请输入姓名"
        show-action
        shape="round"
        class="address-search border"
        clearable
        @clear="onSearch"
        @search="onSearch">
        <div slot="action" @click="onSearch">搜索</div>
      </van-search>
      <!-- 通讯录 -->
      <div class="address-list">
        <van-index-bar :sticky="false">
          <div v-for="item in list" :key="item.index">
            <van-index-anchor :index="item.index" />
            <div v-for="(subItem,subI) in item.children" :key="item.index+subI" class="address-item border">
              <img :src="defaultAvatar">
              <span>{{subItem}}</span>
            </div>
          </div>
        </van-index-bar>
      </div>
  </div>
</template>

<script>
import { NavBar, Icon, IndexBar, IndexAnchor, Search } from 'vant'
import defaultAvatar from '@/assets/img/default-avatar.png'

export default {
  components: {
    [IndexBar.name]: IndexBar,
    [IndexAnchor.name]: IndexAnchor,
    [NavBar.name]: NavBar,
    [Search.name]: Search,
    [Icon.name]: Icon
  },
  data () {
    return {
      defaultAvatar,
      list: [
        {
          index: 'A',
          children: [
            '阿伟1',
            '阿伟2',
            '阿伟3',
            '阿伟4',
            '阿伟5'
          ]
        },
        {
          index: 'D',
          children: [
            '大圣1',
            '大圣2',
            '大圣3',
            '大圣4',
            '大圣5',
            '大圣6'
          ]
        },
        {
          index: 'T',
          children: [
            '汤姆1',
            '汤姆2',
            '汤姆3',
            '汤姆4',
            '汤姆5',
            '汤姆6',
            '汤姆7',
            '汤姆8',
            '汤姆9'
          ]
        },
        {
          index: 'W',
          children: [
            '王安石1',
            '王安石2',
            '王安石3',
            '王安石4',
            '王安石5',
            '王安石6',
            '王安石7'
          ]
        }
      ],
      search: ''
    }
  },
  methods: {
    onSearch () {
      console.log(this.search)
    }
  }
}
</script>

<style  scoped>
.address{
  padding-bottom: 60px;
  position: relative;
}
.address-search{
  position: fixed;
  top:46px;
  left: 0px;
  z-index: 1;
  width: 100%;
}
.address-item{
  height: 60px;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.address-item>img{
  margin: 0 15px;
  width: 40px;
}
.address-list{
  padding-top: 54px;
}

</style>



